html {
    box-sizing: border-box;
    font-size: 62.5%;
}

* {
    font-family: "Lucida Console", monospace;
}
*,
*:before,
*:after {
    box-sizing: inherit;
}

.scroll {
    overflow: hidden;
}

.ReportItem {
    background-image: url(../svg-assets/report-an-item.svg);
    background-repeat: no-repeat;
    background-position: 0 80%;
}

.container {
    display: flex;
    flex-direction: row;
}
.details {
    padding: 3% 0% 0% 5%;
    height: 100vh;
    width: 70%;
}

h1 {
    font-size: 7rem;
}

.reportitem_label {
    display: block;
    font-size: 2.5rem;
    padding-bottom: 1rem;
}

.reportitem_input {
    display: block;
    margin-top: 10px;
    margin-bottom: 10px;
    font-size: 2rem;
    padding: 0.5rem;
}

.location {
    width: 45%;
}

.reportitem_textarea {
    font-size: 2rem;
    display: block;
    width: 50%;
    min-height: 20rem;
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 0.5rem;
}

.small {
    display: inline-block;
}
.btn {
    position: relative;
    display: inline;
    padding: 15px 25px;
    background-color: #ffffff;
    box-shadow: 6px 6px 0 #404040;
    border: solid black 1px;
    transition: all 0.15s linear 0s;
    position: relative;
    margin-bottom: 2%;
    margin-right: 4%;
    margin-left: 4%;
    font-size: 25px;
    top: 40px;
}
.btn:hover {
    transform: translate(2px, 2px);
    box-shadow: 3px 3px 0 #404040;
}

.btn:active {
    transform: translate(6px, 6px);
    box-shadow: none;
}

.reportitem_span {
    position: relative;
    top: 40px;
}

.reportitem_img {
    align-self: center;
    width: 486px;
    height: 475px;
    border: 1px solid black;
}
